<template>
  <right-page :header="rightPageHeader" modal-button-name="OK" :hide-content-padding="rightPageHeader.isHidePadding" :is-list-content="rightPageHeader.hasListContent">
    <li slot="button-group">
      <button class="slds-button slds-button_neutral" @click="changeDefaultStatus">Change Desktop Offline Status</button>
      <button class="slds-button slds-button_neutral" :disabled="disabledNewBtn" @click="newAgentStatus">New Status</button>
    </li>
    <div slot="content" class="list-content">
      <div class="slds-border_bottom agent-title">
        <div class="slds-text-body_regular">Maximum number of statuses: 10 • {{ agentStatuses.length > 1 ? `${ agentStatuses.length} items` : `${ agentStatuses.length} item`}}</div>
      </div>
      <div class="slds-table--header-fixed_container agent-list-content" key="agentStatuses" v-if="agentStatuses.length > 0">
        <div class="slds-scrollable_y scroll-content">
          <table id="agent-status-table" class="slds-table slds-table_bordered slds-table--header-fixed">
            <thead>
              <tr class="slds-line-height_reset">
                <th class="slds-cell-wrap" scope="col" style="width: 3rem;">
                  <span class="slds-truncate slds-cell-fixed"></span>
                </th>
                <th class="slds-cell-wrap priority-title" scope="col" style="width: 90px;">
                  <span class="slds-cell-fixed" style="width: 90px;">
                    <span class="slds-truncate" title="Priority">Priority</span>
                    <tooltip :info-style="'top: 4px;'" content="New chat requests are assigned to agents with higher priority statuses."></tooltip>
                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 12%;">
                  <span class="slds-cell-fixed" style="width: 12%;">
                    <span class="slds-truncate" title="Name">Name</span>
                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 12%;">
                  <span class="slds-cell-fixed" style="width: 12%;">
                    <span class="slds-truncate" title="Active">Active</span>
                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 15%;">
                  <span class="slds-cell-fixed" style="width: 15%;">
                    <span class="slds-truncate" title="Accepts Chats">Accepts Chats</span>
                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 15%;">
                  <span class="slds-cell-fixed" style="width: 15%;">
                    <span class="slds-truncate" title="Offline Default Status">Offline Default Status</span>
                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 15%;">
                  <span class="slds-cell-fixed" style="width: 15%;">
                    <span class="slds-truncate" title="Non-overridable By System">Non-overridable By System</span>
                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 3rem;">
                </th>
              </tr>
            </thead>
            <draggable v-model="activeAgentStatuses" element="tbody" @start="dragStart" @end="dragEnd">
              <tr class="slds-hint-parent list-item" v-for="(agent, index) in activeAgentStatuses" :key="index">
                <th>
                  <span class="slds-media__figure slds-listbox__option-icon list-icon">
                    <span class="slds-icon_container slds-icon-utility-search">
                      <icon name="drag_drop" icon-class-name="slds-icon slds-icon_x-small slds-icon-text-default">
                      </icon>
                    </span>
                  </span>
                </th>
                <th data-label="PRIORITY" scope="row">
                  <div class="slds-truncate">{{ agent.Priority }}</div>
                </th>
                <th data-label="NAME" scope="row">
                  <div class="slds-truncate"><a href="javascript:void(0);" :title="agent.Name" @click="goToDetail(agent)">{{ agent.Name }}</a></div>
                </th>
                <td data-label="ACTIVE">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox4" disabled :checked="agent.IsActive"/>
                        <label class="slds-checkbox__label" for="checkbox4">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
                <td data-label="ACCEPTS CHATS">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox1" disabled :checked="agent.IsAcceptsChats"/>
                        <label class="slds-checkbox__label" for="checkbox1">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
                <td data-label="OFFLINE DEFAULT STATUS">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox2" disabled :checked="agent.IsOfflineDefaultStatus"/>
                        <label class="slds-checkbox__label" for="checkbox2">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
                <td data-label="NON OVERRIDABLE BY SYSTEM">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox3" disabled :checked="agent.IsNonoverridableBySystem"/>
                        <label class="slds-checkbox__label" for="checkbox3">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
                <td data-label="ACTION">
                  <action-menu :actions="activeAgentStatusActions" :item="agent" :concealed-actions="defaultAgentStatusHideActions" @edit="editAgent" @deactivate="openChangeAgentStatusModal($event, 'Deactivate')" v-if="agent.IsOfflineDefaultStatus">
                  </action-menu>
                  <action-menu :actions="activeAgentStatusActions" :item="agent" @edit="editAgent" @deactivate="openChangeAgentStatusModal($event, 'Deactivate')" v-if="!agent.IsOfflineDefaultStatus">
                  </action-menu>
                </td>
              </tr>
            </draggable>
            <tbody class="table-bordered">
              <tr class="slds-hint-parent" v-for="(agent, index) in inactiveAgentStatuses" :key="index">
                <th>
                  <div class="slds-truncate"></div>
                </th>
                <th data-label="PRIORITY" scope="row">
                  <div class="slds-truncate">{{ agent.Priority }}</div>
                </th>
                <th data-label="NAME" scope="row">
                  <div class="slds-truncate"><a href="javascript:void(0);" :title="agent.Name" @click="goToDetail(agent)">{{ agent.Name }}</a></div>
                </th>
                <td data-label="ACTIVE">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox5" disabled :checked="agent.IsActive"/>
                        <label class="slds-checkbox__label" for="checkbox5">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
                <td data-label="ACCEPTS CHATS">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox6" disabled :checked="agent.IsAcceptsChats"/>
                        <label class="slds-checkbox__label" for="checkbox6">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
                <td data-label="OFFLINE DEFAULT STATUS">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox7" disabled :checked="agent.IsOfflineDefaultStatus"/>
                        <label class="slds-checkbox__label" for="checkbox7">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
                <td data-label="NON OVERRIDABLE BY SYSTEM">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox8" disabled :checked="agent.IsNonoverridableBySystem"/>
                        <label class="slds-checkbox__label" for="checkbox8">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
                <td data-label="ACTION">
                  <action-menu :actions="inactiveAgentStatusActions" :item="agent" @edit="editAgent" @activate="openChangeAgentStatusModal($event, 'Activate')">
                  </action-menu>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="no-records" v-else key="agentStatuses">
        <div class="slds-text-body_regular">No Records</div>
      </div>
      <new-agent-status
        ref="NewAgentStatus"
        :edit-agent="editAgentStatus"
        @save="saveEditAgentStatus"
        @hide="cancelNewAgentStatus"
      ></new-agent-status>

      <ld-modal
        ref="modal"
        :title="'Change Offline Default Status'"
        :persistent="isChangeStatus"
      >
        <template #content>
          <div class="slds-p-around_medium picklist-content">
            <loading custom-style="position: relative; padding: 10px" v-if="showLoading">
            </loading>
            <label class="slds-form-element__label" v-if="!showLoading"><abbr class="slds-required">* </abbr>Agent Status</label>
            <input-box :current="defaultAgentStatus" v-if="!showLoading">
            </input-box>
            <div class="slds-form-element__help help-label" v-if="!showLoading">The status to use when the system determines an agent is already offline, for example after a computer goes to the sleep mode.</div>
          </div>
        </template>

        <template #footer>
          <ld-button
            :disabled="isChangeStatus"
            @click="cancelChangeStatus()"
            neutral
          >
            Cancel
          </ld-button>
          <ld-button
            :disabled="isChangeStatus"
            @click="saveChangeAgentStatus()"
            brand
          >
            Change
          </ld-button>
        </template>
      </ld-modal>

      <dropdown :options="options" :current="defaultAgentStatus" @select="selectAgentStatus">
      </dropdown>
    </div>
  </right-page>
</template>

<script>
import draggable from 'vuedraggable'
import NewAgentStatus from '../components/agent_status/NewAgentStatus'
import { getAgentStatuses, sortAgentStatus, toggleAgentStatus, changeDefaultDesktopOfflineStatus, getActiveAgentStatuses, getOfflineDefaultStatus } from '../api/charket-settings'
import HTMLUtil from 'charket-ui/dist/utils/HTMLUtil'
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdButton } from 'charket-ui/dist/ld-button.js'

export default {
  name: 'AgentStatusesSettings',
  components: {
    draggable,
    NewAgentStatus,
    LdModal,
    LdButton
  },
  computed: {
    disabledNewBtn () {
      return this.agentStatuses.length >= 10
    }
  },
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Agent Statuses',
        isHidePadding: true,
        hasListContent: true
      },
      agentStatuses: [],
      activeAgentStatuses: [],
      inactiveAgentStatuses: [],
      activeAgentStatusActions: ['Edit', 'Deactivate'],
      inactiveAgentStatusActions: ['Edit', 'Activate'],
      defaultAgentStatusHideActions: ['Deactivate'],
      selectedAgentStatus: {},
      defaultAgentStatus: {},
      isChangeStatus: false,
      options: [],
      showLoading: false,
      editAgentStatus: {},
      dragStartActiveAgentStatus: [],
      hasRemoting: false,
      remoteIndex: 0,
      remoteAgentStatusIndex: -1,
      remoteAgentStatus: [],
      canUpdateAgentStatus: true
    }
  },
  created () {
    this.fetchAgentStatus()
  },
  mounted () {
    this.$bus.on('click-toast-close', () => {
      this.canUpdateAgentStatus = true
      this.hasRemoting = false
    })
  },
  beforeDestroy () {
    this.$bus.off('click-toast-close')
  },
  methods: {
    async fetchAgentStatus () {
      try {
        this.$bus.emit('show-loading')
        this.agentStatuses = await getAgentStatuses()
        this.activeAgentStatuses = this.agentStatuses.filter(agent => agent.IsActive)
        this.inactiveAgentStatuses = this.agentStatuses.filter(agent => !agent.IsActive)
        this.canUpdateAgentStatus = true
        this.hasRemoting = false
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    async fetchActiveAgentStatus () {
      try {
        this.showLoading = true
        const [activeAgentStatuses, defaultAgentStatus] = await Promise.all([getActiveAgentStatuses(), getOfflineDefaultStatus()])
        this.options = activeAgentStatuses
        this.defaultAgentStatus = defaultAgentStatus
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.showLoading = false
      }
    },
    resetRemoteAndToast () {
      this.$bus.emit('close-toast')
      this.canUpdateAgentStatus = true
      this.hasRemoting = false
    },
    openChangeAgentStatusModal (item, type) {
      let confirmTitle = ''
      let confirmContent = ''
      if (type === 'Deactivate') {
        confirmTitle = 'Deactivate Agent Status'
        confirmContent = 'Are you sure want to deactivate this agent status?'
      } else if (type === 'Activate') {
        confirmTitle = 'Activate Agent Status'
        confirmContent = 'Are you sure want to activate this agent status?'
      }
      this.selectedAgentStatus = item
      this.resetRemoteAndToast()
      const confirm = this.$confirm({
        title: confirmTitle,
        content: confirmContent,
        ok: 'OK'
      })

      confirm.$on('ok', async () => {
        if (!this.selectedAgentStatus.Id) {
          return null
        }
        await this.changeAgentStatusState(this.selectedAgentStatus, confirm)
      })
    },
    newAgentStatus () {
      this.$refs.NewAgentStatus.open()
      this.editAgentStatus = {}
      this.resetRemoteAndToast()
    },
    cancelNewAgentStatus () {
      this.editAgentStatus = {}
    },
    editAgent (item) {
      this.$refs.NewAgentStatus.open()
      this.editAgentStatus = JSON.parse(JSON.stringify(item))
      this.resetRemoteAndToast()
    },
    saveEditAgentStatus () {
      this.editAgentStatus = {}
      this.fetchAgentStatus()
    },
    goToDetail (agent) {
      this.resetRemoteAndToast()
      this.$router.push({ path: `/agent-statuses/agent-statuses-settings/${agent.Id}/detail` })
    },
    changeDefaultStatus () {
      this.$refs.modal.open()
      this.resetRemoteAndToast()
      this.fetchActiveAgentStatus()
    },
    cancelChangeStatus () {
      this.$refs.modal.close()
      this.defaultAgentStatus = {}
    },
    selectAgentStatus (selectedOption) {
      this.defaultAgentStatus = selectedOption
    },
    async saveChangeAgentStatus () {
      try {
        this.isChangeStatus = true
        await changeDefaultDesktopOfflineStatus(this.defaultAgentStatus.Id)
        this.$refs.modal.close()
        this.defaultAgentStatus = {}
        this.$bus.emit('success', { content: 'The change was saved.', type: 'success' })
        this.fetchAgentStatus()
      } catch (error) {
        const errorMessage = this.$parseErrorMessage(error)
        this.$refs.modal.showError(errorMessage)
      } finally {
        this.isChangeStatus = false
      }
    },
    dragStart () {
      this.remoteAgentStatusIndex++
      this.dragStartActiveAgentStatus = []
      this.activeAgentStatuses.map(item => this.dragStartActiveAgentStatus.push(item.Id))
    },
    async dragEnd () {
      let updateAgents = []
      this.remoteAgentStatus.push({ oldAgentStatusIds: this.dragStartActiveAgentStatus, oldAgentStatus: JSON.parse(JSON.stringify(this.activeAgentStatuses)), updateAgentStatus: [], newAgentStatus: [] })
      this.activeAgentStatuses.map((item, index) => {
        if ((index + 1) !== item.Priority) {
          item.Priority = index + 1
          updateAgents.push({ Id: item.Id, Priority: item.Priority })
        }
      })
      this.remoteAgentStatus[this.remoteAgentStatusIndex].updateAgentStatus = JSON.parse(JSON.stringify(updateAgents))
      this.remoteAgentStatus[this.remoteAgentStatusIndex].newAgentStatus = JSON.parse(JSON.stringify(this.activeAgentStatuses))
      if (!this.hasRemoting && this.canUpdateAgentStatus) {
        // 第一个更新agent status的remote
        this.remoteIndex = this.remoteAgentStatus.length - 1
        let currentRemoteAgentStatus = this.remoteAgentStatus[this.remoteAgentStatus.length - 1]
        this.recursiveSortAgentStatus(currentRemoteAgentStatus.oldAgentStatusIds, currentRemoteAgentStatus.updateAgentStatus, currentRemoteAgentStatus.oldAgentStatus)
      }
    },
    recursiveSortAgentStatus (startAgentStatusIds, updateAgentStatus, startAgentStatus) {
      this.hasRemoting = true
      sortAgentStatus(JSON.stringify(startAgentStatusIds), JSON.stringify(updateAgentStatus)).then((result) => {
        if (this.remoteIndex < this.remoteAgentStatus.length - 1) {
          // 最后一个更新agent status的remote
          let newStartAgentStatus = JSON.parse(JSON.stringify(this.remoteAgentStatus[this.remoteIndex].newAgentStatus))
          let newStartAgentStatusIds = []
          newStartAgentStatus.map(item => newStartAgentStatusIds.push(item.Id))
          this.remoteIndex = this.remoteAgentStatus.length - 1
          let newEndAgentStatus = JSON.parse(JSON.stringify(this.remoteAgentStatus[this.remoteIndex].newAgentStatus))
          let updateIdAndPriorities = []
          newEndAgentStatus.map((item, index) => {
            if (item.Id !== newStartAgentStatus[index].Id) {
              updateIdAndPriorities.push({ Id: item.Id, Priority: item.Priority })
            }
          })
          this.recursiveSortAgentStatus(newStartAgentStatusIds, updateIdAndPriorities, newStartAgentStatus)
        } else {
          this.hasRemoting = false
        }
      }, (error) => {
        if (error.type === 'serve_error') {
          let resultData = JSON.parse(HTMLUtil.unescape(error.msg))
          this.$bus.emit('error', { content: resultData.message, type: 'error', hasAction: true })
        } else {
          this.$bus.emit('error', { content: error.msg, type: 'error' })
        }
        this.canUpdateAgentStatus = false
        this.activeAgentStatuses = JSON.parse(JSON.stringify(startAgentStatus))
      })
    },
    async changeAgentStatusState (selectedObj, confirm) {
      try {
        await toggleAgentStatus(selectedObj.Id, !selectedObj.IsActive)
        if (selectedObj.IsActive) {
          this.$bus.emit('success', { content: 'The agent status was deactivated.', type: 'success' })
        } else {
          this.$bus.emit('success', { content: 'The agent status was activated.', type: 'success' })
        }
        confirm.close()
        this.fetchAgentStatus()
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        confirm.resume()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  #agent-status-table tbody {
    transform: none;
  }
  .priority-title {
    position: relative;
    width: 6rem;
  }
  .agent-title {
    height: 48px;
    display: flex;
    align-items: center;
    background-color: rgb(250, 250, 249);
    padding-left: 20px;
  }
  .list-content {
    height: 100%;
    .agent-list-content {
      height: calc(100% - 48px);
      .scroll-content {
        height: 100%;
        padding-bottom: 70px;
      }
      table {
        table-layout: fixed;
        thead {
          span.slds-cell-fixed {
            display: flex;
            align-items: center;
            padding: .25rem .5rem;
          }
          th:first-child span {
            padding-left: 0;
          }
        }
      }
      .table-bordered {
        tr > th, tr > td {
          border-top: 1px solid #dddbda;
        }
      }
    }
  }
  .no-records {
    height: calc(100% - 48px);
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .list-item {
    cursor: pointer;
  }
  .list-icon {
    cursor: move;
    margin-bottom: 2px;
    padding-left: 9px;
  }
  .picklist-content {
    padding: 33px 42px 37px 42px;
  }
  .help-label {
    color: #706E6B;
  }
</style>
